import {
  Drawer,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
} from '@mui/material';
import React, { FC, useMemo } from 'react';
import { graphql, useStaticQuery, Link } from 'gatsby';
import DescriptionSharpIcon from '@mui/icons-material/DescriptionSharp';
import { DRAWER_WIDTH } from './document';

type MdxNode = {
  id?: string;
  frontmatter?: {
    description?: string;
    slug?: string;
    tags?: string[];
    title?: string;
    order?: number;
    icon?: string;
    route?: string;
  };
};

const Nav: FC = () => {
  const {
    allMdx: { nodes },
  } = useStaticQuery(graphql`
    query MyQuery {
      allMdx {
        nodes {
          id
          frontmatter {
            slug
            route
            tags
            title
            order
            description
          }
        }
      }
    }
  `);

  const menu = useMemo(() => {
    return (nodes as MdxNode[]).sort(
      (prev, next) =>
        (prev.frontmatter?.order ?? 1) - (next.frontmatter?.order ?? 1)
    );
  }, [nodes]);

  return (
    <Drawer
      variant="persistent"
      anchor="left"
      open
      sx={{
        width: DRAWER_WIDTH,
        flexShrink: 0,
        '& .MuiDrawer-paper': {
          width: DRAWER_WIDTH,
          boxSizing: 'border-box',
        },
      }}
    >
      <Toolbar />
      <List>
        {menu?.map((node: MdxNode) => (
          <Link
            key={node.id}
            to={node.frontmatter?.slug || '/404'}
            partiallyActive
            activeStyle={{ color: 'red' }}
          >
            <ListItemButton>
              <ListItemIcon>
                <DescriptionSharpIcon />
              </ListItemIcon>
              <ListItemText primary={node.frontmatter?.title} />
            </ListItemButton>
          </Link>
        ))}
      </List>
    </Drawer>
  );
};

export default Nav;
